.list {
  margin-top: 20px;
  width: max-content;
}

.list-item {
  display: flex;
  margin: 10px 0px;
  width: 100%;
}

.list-item__action {
  flex: 1;
  text-align: right;
}

.list-item__state {
  font-size: 30px;
}

.list-item__content {
  font-size: 30px;
}

.list-item__delete {
  margin-left: 20px;
}

#root {
  display: flex;
  justify-content: center;
  width: 75%;
  margin: 100px auto;
  border: gray 1px solid;
  align-items: center;
}

.btn-danger {
  margin-right: 10px;
}

.completed .list-item__content {
  text-decoration: line-through;
}

@media only screen and (max-width: 600px) {
  #root {
    width: 90%;
  }
  .filters .cURLoP {
    left: 0px;
  }
  .filters__completed {
    margin-left: 50px;
  }
  .list {
    width: 280px;
  }
  .list-item__action {
    width: 150px;
  }
  .list-item__content {
    width: 100px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space : nowrap;
  }
}

.filters {
  display: flex;
}